import React from "react"
import style from "../assets/scss/index.module.scss";
import Layout from "../component/layout";
import {graphql, useStaticQuery} from "gatsby";
import classNames from "classnames";
import Img from "gatsby-image"
import {Introduction} from "../component/about/introduction";
import {Experience} from "../component/about/experience";

const About = (props: any) => {
    return <Layout title={"關於我"}>
        <div className={classNames(style.gBrdAround, style.gBrdGrayLightV4, style.gPa20, style.gMb40)}>
            <div className={style.row}>
                <div className={classNames(style.colLg4, style.gMb40, style.gMb0Lg)}>
                    <div className={style.gPy30Lg}>
                        <Img
                            className={classNames(style.imgFluid, style.w100)}
                            fluid={props.data.imageSharp.fluid}
                            fadeIn={true}
                            alt="Bruno Jan"/>
                    </div>
                </div>
                <div className={style.colLg8}>
                    <Introduction/>
                </div>
            </div>
        </div>
        <div className={style.colLg12}>
            <Experience />
        </div>
    </Layout>
}

export const pageQuery = graphql`
    query {
        imageSharp(fluid: {originalName: {eq: "myimage.jpg"}}) {
            fluid(maxWidth: 400) {
                ...GatsbyImageSharpFluid
            }
        }
    }
`

export default About;