import React from "react";
import classNames from "classnames";
import * as style from "../../assets/scss/index.module.scss";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {graphql, useStaticQuery} from "gatsby";

export const Introduction: React.FunctionComponent = React.memo(() => {
    const {allConfigYaml:{edges: [{node: data}]} }: any = useStaticQuery(graphql`
        query about {
            allConfigYaml {
                edges {
                    node {
                        about
                        job_title
                        links {
                            icon {
                                iconName
                                prefix
                            }
                            title
                            url
                        }
                    }
                }
            }
        }
    `)

    return <>
        <div className={classNames(style.dFlex, style.alignItemsCenter, style.justifyContentSmBetween, style.gMb5)}>
            <h2 className={classNames(style.gFontWeight300, style.gMr10)}>Bruno Jan</h2>
            <ul className={classNames(style.listInline, style.mb0)}>
                {
                    data.links.map((item: any) =>{
                        return <li className={classNames(style.listInlineItem, style.gMx2)} key={item.title}>
                            <a className={classNames(style.uIconV1,
                                style.uIconSizeSm,
                                style.uIconSlideUpHover,
                                style.gColorGrayLightV1,
                                style.gBgGrayLightV5,
                                style.gColorGrayLightV1Hover,
                                style.roundedCircle)}
                               href={item.url}
                               target={"_blank"}>
                                <i className={classNames(style.gFontSizeDefault, style.gLineHeight1, style.uIconElemRegular)}>
                                    <FontAwesomeIcon width={8} height={14}
                                                     icon={item.icon}/>
                                </i>
                                <i className={classNames(style.gFontSizeDefault, style.gLineHeight1, style.uIconElemHover)}>
                                    <FontAwesomeIcon width={8} height={14}
                                                     icon={item.icon}/>
                                </i>
                            </a>
                        </li>
                    })
                }
            </ul>
        </div>
        <h6 className={classNames(style.h6, style.gFontWeight300, style.gMb10)}>
            <i className={classNames(style.gPosRel, style.gTop1, style.gMr5, style.gColorGrayDarkV5)}>
                <FontAwesomeIcon width={10.5} height={14} icon={{prefix: "fas", iconName: 'award'}}/>
            </i>
            {data.job_title}
        </h6>
        <hr className={classNames(style.gBrdGrayLightV4, style.gMy20)}/>
        <p className={classNames(style.lead, style.gLineHeight18)}>
            {data.about}
        </p>
        <hr className={classNames(style.gBrdGrayLightV4, style.gMy20)}/>
    </>;
});
