import { faBriefcase } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classNames from "classnames";
import { graphql, useStaticQuery } from "gatsby";
import React from "react";
import style from "../../assets/scss/index.module.scss";

function WorkExperience(props: any) {
    const {
        frontmatter: {
            title,
            during,
            company
        },
        headings
    } = props;

    return <li className={ classNames(style.colMd12,
                                      style.gBrdBottom,
                                      style.gBrd0Md,
                                      style.gBrdGrayLightV4,
                                      style.gPb30,
                                      style.gPb0Md,
                                      style.gMb30,
                                      style.gMb0Md,
                                      style.animateAnimated,
                                      style.animateFadeInDown) }>
        <div className={ classNames(style.row) }>
            <div
                className={ classNames(style.colMd3, style.gPy15, style.textMdRight, style.gPr40Md, style.gMb20, style.gMb0Md) }>
                <h5 className={ classNames(style.h5, style.gFontWeight300) }>{ title }</h5>
                <h6 className={ classNames(style.h6, style.gFontWeight300, style.mb0) }>{ during }</h6>
            </div>
            <div className={ classNames(style.colMd9,
                                        style.alignSelfCenter,
                                        style.gOrientationLeft,
                                        style.gPl40Md,
                                        style.gBrdDarkblueLeft,
                                        style.gBrd5) }>
                <div
                    className={ classNames(style.gHiddenSmDown, style.uTimelineV2Icon, style.gTop35) }>
                    <i className={ classNames(style.dBlock,
                                              style.gWidth18,
                                              style.gHeight18,
                                              style.gBgPurple,
                                              style.gBrdAround,
                                              style.gBrd4,
                                              style.gBrdGrayLightV4,
                                              style.roundedCircle) }/>
                </div>
                <article className={ classNames(style.gPa10Md) }>
                    <h4 className={ classNames(style.h4, style.gFontWeight300) }>{ company }</h4>
                    {
                        headings.map(({ value }: any) => {
                            return <p key={ value }>{ value }</p>
                        })
                    }
                </article>
            </div>
        </div>
    </li>;
}

export const Experience: React.FunctionComponent = () => {
    const { allMdx: { edges } } = useStaticQuery(graphql`
        query {
            allMdx(filter: {frontmatter: {category: {eq: "experiences"}}}, sort: {fields: frontmatter___during, order: DESC}) {
                edges {
                    node {
                        frontmatter {
                            company
                            title
                            during
                        }
                        headings {
                            value
                        }
                    }
                }
            }
        }
    `)

    return <div className={ classNames(style.card, style.border0, style.gMb40) }>
        <div
            className={ classNames(style.cardHeader, style.dFlex, style.alignItemsCenter, style.justifyContentBetween, style.gBgGrayLightV5, style.border0, style.gMb15) }>
            <h6 className={ classNames(style.h6, style.mb0) }>
                <FontAwesomeIcon width={ 14 } icon={ faBriefcase } className={ classNames(style.gPosRel, style.gTop1, style.gMr5) }/>
                Experience
            </h6>
        </div>
        <div className={ classNames(style.uInfoV11, style.gBgWhiteGradientV1After, style.gHeight500, style.gPa0) }>
            <div className={ classNames(style.cScrollbar, style.gPb40) }>
                <ul className={ classNames(style.row, style.uTimelineV2Wrap, style.listUnstyled) }>
                    {
                        edges.map(({ node }: any, index: number) => {
                            return <WorkExperience key={ index } { ...node } />
                        })
                    }
                </ul>
            </div>

        </div>
    </div>
};
